@font-face {
  font-family: "TSCu_SaiIndira";
  src: url("/fonts/TSCuSaiIndira.woff2") format("woff2"),
    url("/fonts/TSCuSaiIndira.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

.tamil {
  font-size: 24px;
  font-family: TSCu_SaiIndira;
}

.copyright {
  /* font-size: 0.875em; */
  /* margin: 10; */
  /* padding-left: 10; */
  color: #007a78;
  margin-left: 30px;
}



:root {
  box-sizing: border-box;
  font-size: calc(1vw + 0.6em);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "TSCu_SaiIndira", Helvetica, Arial, sans-serif;
}

/*
 * Links
 */
a:link {
  color: #1476b8;
  font-weight: bold;
  text-decoration: none;
}
a:visited {
  color: #1430b8;
}
a:hover {
  text-decoration: underline;
}
a:active {
  color: #b81414;
}

/*
 * Header section
 */
.page-header {
  padding: 0.4em 1em;
  background-color: #fff;
}

@media (min-width: 35em) {
  .page-header {
    padding: 1em;
  }
}

.title > h1 {
  color: #333;
  text-transform: uppercase;
  font-size: 1.5rem;
  margin: 0.2em 0;
}

@media (min-width: 35em) {
  .title > h1 {
    font-size: 2.25rem;
  }
}

.slogan {
  color: #888;
  font-size: 0.875em;
  margin: 0;
}


/*
 * Hero image
 */
.hero {
  padding: 2em 1em;
  text-align: center;
  /* background-image: url(coffee-beans.jpg); */
  background-size: 100%;
  color: #fff;
  text-shadow: 0.1em 0.1em 0.3em #000;
}

@media (min-width: 35em) {
  .hero {
    padding: 5em 3em;
    font-size: 1.2rem;
  }
}

/*
 * Main section
 */
main {
  padding: 2em;
}

@media (min-width: 35em) {
  main {
    padding: 2em 1em;
  }
}

.subtitle {
  margin-top: 1.5em;
  margin-bottom: 1.5em;
  font-size: 0.875rem;
  text-transform: uppercase;
}

/*
 * Menu
 */
.menu {
  position: relative;
}

.menu-toggle {
  position: absolute;
  top: -1.2em;
  right: 0.1em;
  border: 0;
  background-color: transparent;
  font-size: 3em;
  width: 1em;
  height: 1em;
  line-height: 0.4;
  text-indent: 5em;
  white-space: nowrap;
  overflow: hidden;
}
.menu-toggle::after {
  position: absolute;
  top: 0.2em;
  left: 0.2em;
  display: block;
  content: "\2261";
  text-indent: 0;
}

.menu-dropdown {
  display: none;
  position: absolute;
  right: 0;
  left: 0;
  margin: 0;
}

.menu.is-open .menu-dropdown {
  display: block;
}

.nav-menu {
  margin: 0;
  padding-left: 0;
  border: 1px solid #ccc;
  list-style: none;
  background-color: #000;
  color: #fff;
}

.nav-menu > li + li {
  border-top: 1px solid #ccc;
}

.nav-menu > li > a {
  display: block;
  padding: 0.8em 1em;
  color: #fff;
  font-weight: normal;
}

@media (min-width: 30em) {
  .flyin-grid {
    display: flex;
    flex-wrap: wrap;
    /* margin-top: 0.2em;
    margin-bottom: 0.2em; */
    margin-left: 0.1em;
    margin-right: 0.1em;
    /* margin: 0.5rem; */
  }

  .flyin-grid__item {
    flex: 1 1 300px;
    margin-left: 0.1em;
    margin-right: 0.1em;
    max-width: 300px;
  }

  @supports (display: grid) {
    .flyin-grid {
      display: grid;
      /* grid-template-columns: repeat(auto-fit, minmax(300px, 1fr)); */
      grid-gap: 2em;
    }

    .flyin-grid__item {
      max-width: initial;
      margin: 0;
    }
  }
}

.card {
  border-color: blue;
  border-style: solid;
  margin-bottom: 0.5em;
  padding-top: 0.5em;
  padding-right: 0.5em;
  padding-left: 0.5em;
  /* padding-bottom: 0.1em; */
  /* padding: 0.5em; */

  background-color: white;
  /* color: hsl(210, 15%, 20%); */
  box-shadow: 0.2em 0.5em 1em rgb(246, 218, 7);
}

.card > img {
  width: 100%;
}

/* Tabbed display */
.tabs {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 0px solid blue;
}

.tabs > button {
  margin-bottom: -1px;
  padding: 0.6em 1.2em 0.4em;
  border: 1px solid blue;
  border-top-right-radius: 1em;
  border-top-left-radius: 1em;
  color: black;
  background: transparent;
  font-size: 0.8rem;
  font-family: "TSCu_SaiIndira", Glober, Helvetica, sans-serif;
  cursor: pointer;
}
/* 
.tabs > button svg path {
  fill: transparent;
  stroke: currentColor;
  stroke-width: 5;
} */

.tabs > button.tabs__repost svg {
  width: 1.3em;
  vertical-align: -0.2em;
  stroke-width: 10;
}

.tabs > button.is-active,
.tabs > button:hover {
  background-color: #fff;
}

.tabs > button.is-active svg path,
.tabs > button:hover svg path {
  fill: currentColor;
  stroke-width: 0;
}

.tabs > button.is-active.tabs__reply,
.tabs > button:hover.tabs__reply {
  color: #2b78ca;
}

.tabs > button.is-active.tabs__like,
.tabs > button:hover.tabs__like {
  color: #f00544;
}

.tabs > button.is-active.tabs__repost,
.tabs > button:hover.tabs__repost {
  color: #007a78;
}

.tabs > button + button {
  border-left: 0;
}

.tabs > button.is-active {
  border-bottom: 0;
}

.tabs svg {
  display: inline-block;
  width: 1em;
  height: 1em;
  vertical-align: -0.3em;
}

.tab-pane {
  display: none;
  font-size: 1rem;
}

.tab-pane.is-active {
  display: block;
}

.tabs--light > button {
  border-radius: 0;
}

.tabs--light .tabs__reply {
  -webkit-box-flex: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
}

.tab-pane--light {
  padding: 1em;
}

.tab-pane--light .tab-pane__banner {
  margin: 0 -1rem 2rem;
  padding: 0.4rem 1rem;
  background-color: #f0f5fa;
}

.btn-play {
  padding: 0.6em 1em;
  border: 0;
  background-color: hsl(72, 50%, 50%);
  color: black;
  font: inherit;
  transition: background-color 0.3s linear;
}

span {
  font-size: 16px;
  font-family: TSCu_SaiIndira;
  
}

span.group {
  font-size: 16px;
  font-family: TSCu_SaiIndira;
  text-align: right;
}